.scrollToTop{
    z-index: 9999;
    position: fixed;
    bottom: 20px;
    right: 30px;
    padding: 8px;
    border-radius: 5px;
    border: none;
    opacity: 0;
    cursor: pointer;
    pointer-events: none;
    background: var(--primary-color);
    transition: all .3s ease;
    box-shadow: 3px 3px 10px var(--gray-color);
    .icon-scroll{
      font-size: 22px;
      color: var(--white-color);
    }
    &.active{
        opacity: 1;
        pointer-events:visible;
    }
  }

@media screen and (max-width: 767px) {
  .scrollToTop{
    bottom: 70px;
    right: 20px;
    padding: 5px;
  }
}